<template>
  <div class="circle-chart">
    <div class="table-container">
      <svg :width="tableWidth" :height="tableHeight" class="circle-svg">
        <!-- Grid lines -->
        <g class="grid">
          <!-- Horizontal lines -->
          <line :x1="labelWidth" :y1="0" :x2="tableWidth" :y2="0" stroke="#ddd" stroke-width="1" />
          <line :x1="labelWidth" :y1="headerHeight" :x2="tableWidth" :y2="headerHeight" stroke="#ddd" stroke-width="1" />
          <line :x1="labelWidth" :y1="headerHeight + rowHeight" :x2="tableWidth" :y2="headerHeight + rowHeight" stroke="#ddd" stroke-width="1" />
          <line :x1="labelWidth" :y1="headerHeight + rowHeight * 2" :x2="tableWidth" :y2="headerHeight + rowHeight * 2" stroke="#ddd" stroke-width="1" />
          
          <!-- Vertical lines -->
          <line :x1="labelWidth" :y1="0" :x2="labelWidth" :y2="headerHeight + rowHeight * 2" stroke="#ddd" stroke-width="1" />
          <line
            v-for="index in criteriaData.length"
            :key="'vline-' + index"
            :x1="labelWidth + index * cellWidth"
            :y1="0"
            :x2="labelWidth + index * cellWidth"
            :y2="headerHeight + rowHeight * 2"
            stroke="#ddd"
            stroke-width="1"
          />
        </g>

        <!-- Header row with criterion names -->
        <g class="headers">
          <text
            v-for="(data, index) in criteriaData"
            :key="'header-' + data.criterionId"
            :x="labelWidth + index * cellWidth + cellWidth / 2"
            :y="headerHeight / 2 + 5"
            text-anchor="middle"
            font-size="13"
            font-weight="500"
            fill="#333"
          >
            {{ data.criterionName }}
          </text>
        </g>

        <!-- Row labels -->
        <text
          :x="labelWidth / 2"
          :y="headerHeight + rowHeight / 2 + 5"
          text-anchor="middle"
          font-size="18"
          font-weight="500"
          fill="#333"
        >
          +
        </text>
        <text
          :x="labelWidth / 2"
          :y="headerHeight + rowHeight * 1.5 + 5"
          text-anchor="middle"
          font-size="18"
          font-weight="500"
          fill="#333"
        >
          -
        </text>

        <!-- Circles and connecting lines -->
        <g v-for="(data, index) in criteriaData" :key="'circles-' + data.criterionId">
          <!-- Connecting line -->
          <line
            v-if="data.favorable > 0 && data.unfavorable > 0"
            :x1="getCellCenterX(index)"
            :y1="getFavorableY() + getCircleRadius(data.favorable)"
            :x2="getCellCenterX(index)"
            :y2="getUnfavorableY() - getCircleRadius(data.unfavorable)"
            stroke="#999"
            stroke-width="1"
            stroke-dasharray="3,3"
          />
          
          <!-- Favorable circle (green) -->
          <circle
            v-if="data.favorable > 0"
            :cx="getCellCenterX(index)"
            :cy="getFavorableY()"
            :r="getCircleRadius(data.favorable)"
            fill="#4CAF50"
            stroke="white"
            stroke-width="2"
          />
          
          <!-- Unfavorable circle (red) -->
          <circle
            v-if="data.unfavorable > 0"
            :cx="getCellCenterX(index)"
            :cy="getUnfavorableY()"
            :r="getCircleRadius(data.unfavorable)"
            fill="#F44336"
            stroke="white"
            stroke-width="2"
          />
          
          <!-- Favorable count text -->
          <text
            v-if="data.favorable > 0"
            :x="getCellCenterX(index)"
            :y="getFavorableY() + 5"
            text-anchor="middle"
            font-size="14"
            font-weight="bold"
            fill="white"
          >
            {{ data.favorable }}
          </text>
          
          <!-- Unfavorable count text -->
          <text
            v-if="data.unfavorable > 0"
            :x="getCellCenterX(index)"
            :y="getUnfavorableY() + 5"
            text-anchor="middle"
            font-size="14"
            font-weight="bold"
            fill="white"
          >
            {{ data.unfavorable }}
          </text>
        </g>

        <!-- Criterion label in header row -->
        <text
          :x="labelWidth / 2"
          :y="headerHeight / 2 + 5"
          text-anchor="middle"
          font-size="11"
          font-weight="500"
          fill="#666"
        >
          Criterion
        </text>
      </svg>

      <!-- Footer with collective attitude -->
      <div class="footer-container">
        <div class="footer-label">
          <span>Collective attitude</span>
        </div>
        <div class="footer-values">
          <div
            v-for="(data, index) in criteriaData"
            :key="'footer-' + data.criterionId"
            class="attitude-cell"
            :style="{ left: (labelWidth + index * cellWidth) + 'px', width: cellWidth + 'px' }"
          >
            {{ data.attitude.toFixed(2) }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, computed, PropType } from "vue";
import { NormalizedArgument, NormalizedObject } from "@/@types";

export default defineComponent({
  name: "CircleChart",
  props: {
    arguments: {
      type: Array as PropType<NormalizedArgument[]>,
      required: true,
    },
    criterionsMap: {
      type: Object as PropType<NormalizedObject | null>,
      default: null,
    },
  },
  setup(props) {
    const cellWidth = 180;
    const labelWidth = 60;
    const headerHeight = 60;
    const rowHeight = 250;
    const minRadius = 20;
    const maxRadius = 70;

    // Group arguments by criterion
    const criteriaData = computed(() => {
      const grouped = new Map<number, { favorable: number; unfavorable: number }>();
      
      props.arguments.forEach(arg => {
        if (!grouped.has(arg.criterion)) {
          grouped.set(arg.criterion, { favorable: 0, unfavorable: 0 });
        }
        const entry = grouped.get(arg.criterion)!;
        if (arg.favorable) {
          entry.favorable++;
        } else {
          entry.unfavorable++;
        }
      });

      return Array.from(grouped.entries()).map(([criterionId, counts]) => {
        const total = counts.favorable + counts.unfavorable;
        const attitude = total === 0 ? 0 : counts.favorable / total;
        const criterionName = props.criterionsMap?.[criterionId]?.name || `Criterion ${criterionId}`;
        
        return {
          criterionId,
          criterionName,
          favorable: counts.favorable,
          unfavorable: counts.unfavorable,
          attitude,
        };
      });
    });

    const tableWidth = computed(() => {
      return labelWidth + criteriaData.value.length * cellWidth;
    });

    const tableHeight = headerHeight + rowHeight * 2;

    function getCellCenterX(index: number): number {
      return labelWidth + index * cellWidth + cellWidth / 2;
    }

    function getFavorableY(): number {
      return headerHeight + rowHeight / 2;
    }

    function getUnfavorableY(): number {
      return headerHeight + rowHeight * 1.5;
    }

    function getCircleRadius(count: number): number {
      if (count === 0) return 0;
      const maxCount = Math.max(...criteriaData.value.flatMap(d => [d.favorable, d.unfavorable]));
      if (maxCount === 0) return minRadius;
      const scale = Math.sqrt(count / maxCount);
      return minRadius + (maxRadius - minRadius) * scale;
    }

    return {
      criteriaData,
      tableWidth,
      tableHeight,
      labelWidth,
      headerHeight,
      rowHeight,
      cellWidth,
      getCellCenterX,
      getFavorableY,
      getUnfavorableY,
      getCircleRadius,
    };
  },
});
</script>

<style scoped>
.circle-chart {
  width: 100%;
  overflow-x: auto;
  padding: 20px;
  background: white;
}

.table-container {
  display: inline-block;
  min-width: 100%;
}

.circle-svg {
  display: block;
}

.footer-container {
  display: flex;
  align-items: center;
  border-left: 1px solid #ddd;
  border-right: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  background: #fafafa;
  height: 60px;
}

.footer-label {
  width: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 10px;
  color: #333;
  font-weight: 500;
  border-right: 1px solid #ddd;
  height: 100%;
}

.footer-label span {
  text-align: center;
  line-height: 1.2;
  writing-mode: vertical-rl;
  transform: rotate(180deg);
}

.footer-values {
  flex: 1;
  position: relative;
  height: 100%;
}

.attitude-cell {
  position: absolute;
  top: 0;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  font-weight: 600;
  color: #333;
  border-right: 1px solid #ddd;
}

:deep(.v-btn) {
  min-width: 18px !important;
  width: 18px !important;
  height: 18px !important;
}

:deep(.v-btn .v-icon) {
  font-size: 12px !important;
}
</style>
